<template>
  <div class="cart__mask" v-if="showChart" @click="showChart = !showChart"></div>
  <div class="cart">
    <div class="product" v-if="showChart">
      <div class="product__header">
        <div class="product__header__all"
             @click="setCartItemChecked(shopId, calculations.allChecked)">
          <span class="iconfont"
                v-html="calculations.allChecked ? '&#xe8c3;' : '&#xe8ad;'"></span>
          <span>全选</span>
        </div>
        <div class="product__header__blank"></div>
        <div class="product__header__clear" @click="cleanCartProducts(shopId); showChart = false">清空购物车</div>
      </div>
      <template v-for="item in calculations.productList" :key="item._id">
        <div v-if="item.count > 0" class="product__item">
          <span class="product__item__checked iconfont"
                v-html="item.check ? '&#xe8c3;' : '&#xe8ad;'"
                @click="changeCartItemChecked(shopId, item._id)"></span>
          <img class="product__item__img" :src="item.imgUrl" alt="">
          <div class="product__item__detail">
            <div class="product__item__title">{{ item.name }}</div>
            <div class="product__item__price">
              <span class="product__item__yen">&yen;</span>
              <span class="product__item__now">{{ item.price }}</span>
              <span class="product__item__origin">&yen;{{ item.oldPrice }}</span>
            </div>
          </div>
          <div class="product__number">
            <span class="iconfont product__number__minus"
                  @click="changeCartItemInfo(shopId, item._id, item, -1)"
                  v-if="item.count > 0">&#xe8b1;</span>
            <span class="product__number__count"
                  v-if="item.count > 0">{{ item.count || 0 }}</span>
            <span class="iconfont product__number__plus"
                  @click="changeCartItemInfo(shopId, item._id, item, 1)">&#xe8c8;</span>
          </div>
        </div>
      </template>
    </div>
    <div class="check">
      <div class="check__icon" @click="calculations.sum > 0 && (showChart = !showChart)">
        <img class="check__icon__img"
             src="http://www.dell-lee.com/imgs/vue3/basket.png" alt="">
        <div class="check__icon__tag" v-if="calculations.total > 0">{{ calculations.total }}</div>
      </div>
      <div class="check__info">
        总计：<span class="check__info__price">&yen;{{ calculations.price }}</span>
      </div>
      <router-link :to="calculations.total && (`/orderConfirmation/${shopId}`)">
        <div class="check__btn">
          去结算
        </div>
      </router-link>
    </div>
  </div>
</template>

<script>
import { ref, computed } from 'vue'
import { useRoute } from 'vue-router'
import { useCommonCartEffect } from '@/effects/cartEffects'
import { task } from '@vue/cli-plugin-eslint/ui/taskDescriptor'

// 获取购物车信息逻辑
const useCartEffect = () => {
  const route = useRoute()
  const shopId = route.params.id
  const showChart = ref(false)
  const {
    cartList,
    changeCartItemInfo,
    changeCartItemChecked,
    cleanCartProducts,
    setCartItemChecked
  } = useCommonCartEffect()

  const calculations = computed(() => {
    const productList = cartList[shopId]?.productList
    let sum = 0
    let total = 0
    let price = 0
    let allChecked = true
    for (const key in productList) {
      const product = productList[key]
      sum += product.count
      product.check && (total += product.count)
      product.check && (price += product.price * product.count)
      !product.check && (allChecked = product.check)
    }
    !sum && (showChart.value = false)
    return {
      productList,
      sum,
      total,
      price: price.toFixed(2),
      allChecked
    }
  })

  return {
    showChart,
    calculations,
    shopId,
    changeCartItemInfo,
    changeCartItemChecked,
    cleanCartProducts,
    setCartItemChecked
  }
}

export default {
  name: 'Cart',
  computed: {
    task () {
      return task
    }
  },
  setup () {
    return {
      ...useCartEffect()
    }
  }
}
</script>

<style lang="scss" scoped>
@import '../../style/variables';
@import "../../style/mixins";

.cart__mask {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: $content-notice-fontColor;
}

.cart {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  background: $bgColor;
}

.product {
  padding: 0 .18rem;
  //padding: 0 .18rem .16rem;

  &__header {
    height: .52rem;
    line-height: .52rem;
    border-bottom: .01rem solid $content-bgColor;
    margin: 0 -.18rem;
    padding: 0 .18rem;
    display: flex;

    &__all {
      font-size: .14rem;
      color: $content-fontColor;

      .iconfont {
        font-size: .25rem;
        color: $btn-bgColor;
        vertical-align: top;
        margin-right: .08rem;
      }
    }

    &__blank {
      flex: 1;
    }

    &__clear {
      font-size: .14rem;
      color: $content-fontColor;
    }
  }

  &__item {
    display: flex;
    padding-top: .16rem;
    position: relative;

    &:last-child {
      padding-bottom: .16rem;
    }

    &__checked {
      color: $btn-bgColor;
      font-size: .25rem;
      line-height: .46rem;
    }

    &__img {
      width: .46rem;
      height: .46rem;
      margin: 0 .16rem;
    }

    &__detail {
      flex: 1;
      overflow: hidden;
    }

    &__title {
      line-height: .2rem;
      font-size: .14rem;
      font-weight: bold;
      color: $content-fontColor;
      @include ellipsis;
    }

    &__price {
      line-height: .2rem;
      font-size: .14rem;
      color: $highlight-fontColor;
      margin: .06rem 0 0;
    }

    &__yen {
      font-size: .1rem;
    }

    &__now {
      font-weight: bold;
    }

    &__origin {
      font-size: .1rem;
      color: $light-fontColor;
      text-decoration: line-through;
      margin-left: .06rem;
    }
  }

  &__number {
    line-height: .46rem;

    .iconfont {
      font-size: .25rem;
    }

    &__minus {
      color: $medium-fontColor;
    }

    &__plus {
      color: $btn-bgColor;
    }

    &__count {
      font-size: .14rem;
      color: $content-fontColor;
      margin: 0 .1rem;
      vertical-align: top;
    }
  }
}

.check {
  display: flex;
  height: .49rem;
  line-height: .49rem;
  box-shadow: 0 -.01rem .01rem 0 $content-bgColor;

  &__icon {
    width: .76rem;
    text-align: center;
    position: relative;

    &__img {
      width: .28rem;
      height: .26rem;
      margin-top: .11rem;
    }

    &__tag {
      position: absolute;
      top: .07rem;
      right: .19rem;
      //width: .2rem;
      min-width: .2rem;
      height: .2rem;
      line-height: .2rem;
      font-size: .16rem;
      color: $bgColor;
      background: $highlight-fontColor;
      //border-radius: 50%;
      padding: .02rem;
      border-radius: .12rem;
      transform: scale(.5);
      transform-origin: right top;
    }
  }

  &__info {
    flex: 1;
    padding-left: .08rem;
    font-size: .12rem;
    color: $content-fontColor;

    &__price {
      font-size: .18rem;
      color: $highlight-fontColor;
      font-weight: bold;
      vertical-align: top;
    }
  }

  &__btn {
    width: .98rem;
    background: #4FB0F9;
    color: $bgColor;
    font-size: .14rem;
    text-align: center;
  }

  a {
    text-decoration: none
  }
}
</style>
