<template>
  <div class="content">
    <div class="category">
      <div class="category__item" v-for="(item, index) in categories" :key="index"
           :class="{'category__item--active': currentTab === item.tab}"
           @click="currentTab = item.tab">{{ item.name }}
      </div>
    </div>
    <div class="product">
      <div class="product__item" v-for="item in contentList" :key="item._id">
        <img class="product__item__img" :src="item.imgUrl" alt="">
        <div class="product__item__detail">
          <div class="product__item__title">{{ item.name }}</div>
          <div class="product__item__sales">月售{{ item.sales }}件</div>
          <div class="product__item__price">
            <span class="product__item__yen">&yen;</span>
            <span class="product__item__now">{{ item.price }}</span>
            <span class="product__item__origin">&yen;{{ item.oldPrice }}</span>
          </div>
        </div>
        <div class="product__number">
          <span class="iconfont product__number__minus"
                @click="changeCartItemInfo(shopId, item._id, item, -1)"
                v-if="getProductCartCount(shopId, item._id) > 0">&#xe8b1;</span>
          <span class="product__number__count"
                v-if="getProductCartCount(shopId, item._id) > 0">
            {{ getProductCartCount(shopId, item._id) }}</span>
          <span class="iconfont product__number__plus"
                @click="changeCartItemInfo(shopId, item._id, item, 1)">&#xe8c8;</span>
        </div>
      </div>
    </div>
  </div>
  <Toast v-if="toastInfo.show" :message="toastInfo.message"/>
</template>

<script>
import { ref, reactive, toRefs, watch } from 'vue'
import { useRoute } from 'vue-router'
import { get } from '@/utils/request'
import { useCommonCartEffect } from '@/effects/cartEffects'
import Toast, { useToastEffect } from '@/components/Toast.vue'

const categories = [{
  name: '全部商品',
  tab: 'all'
}, {
  name: '秒杀',
  tab: 'seckill'
}, {
  name: '新鲜水果',
  tab: 'fruit'
}, {
  name: '休闲食品',
  tab: 'snacks'
}, {
  name: '时令蔬菜',
  tab: 'vegetable'
}, {
  name: '肉蛋家禽',
  tab: 'meat'
}]

const useCartEffect = (shopId, props) => {
  const {
    cartList,
    changeCartItemInfo,
    changeShopName
  } = useCommonCartEffect()

  watch(() => props.shopName, () => {
    changeShopName(shopId, props.shopName)
  })

  const getProductCartCount = (shopId, productId) => {
    return cartList[shopId]?.productList?.[productId]?.count || 0
  }

  return {
    changeCartItemInfo,
    getProductCartCount
  }
}

export default {
  name: 'Content',
  components: { Toast },
  props: ['shopName'],
  setup (props) {
    const currentTab = ref(categories[0].tab)
    const data = reactive({ contentList: [] })

    const {
      toastInfo,
      showToast
    } = useToastEffect()

    const route = useRoute()
    const shopId = route.params.id
    const getContentData = async (tab) => {
      try {
        const result = await get(`/api/shop/${shopId}s/products`, {
          tab: tab
        })
        if (result.errno !== 0) return showToast(result.desc)
        // data.contentList = result.data
        data.contentList.push(...result.data)
      } catch (e) {
        showToast(e.message)
      }
    }

    getContentData(currentTab.value)

    watch(currentTab, () => {
      getContentData(currentTab.value)
    })
    // const handleCategoryClick = (tab) => {
    //   currentTab.value = tab
    //   getContentData(currentTab.value)
    // }
    //
    // watchEffect(() => {
    //   getContentData(currentTab.value)
    // })

    return {
      categories,
      toastInfo,
      currentTab,
      ...toRefs(data),
      shopId,
      ...useCartEffect(shopId, props)
      // handleCategoryClick
    }
  }
}
</script>

<style lang="scss" scoped>
@import "../../style/variables";
@import "../../style/mixins";

.content {
  display: flex;
  position: absolute;
  top: 1.54rem;
  bottom: .5rem;
  left: 0;
  right: 0;
}

.category {
  width: .76rem;
  background: $search-bgColor;
  overflow: auto;

  &__item {
    height: .4rem;
    line-height: .4rem;
    text-align: center;
    font-size: .14rem;
    color: $content-fontColor;

    &--active {
      background: $bgColor;
    }
  }
}

.product {
  flex: 1;
  overflow: auto;
  padding: 0 .18rem 0 .16rem;

  &__item {
    display: flex;
    padding: .12rem 0;
    border-bottom: .01rem solid $content-bgColor;
    position: relative;

    &__img {
      width: .68rem;
      height: .68rem;
      margin-right: .16rem;
    }

    &__detail {
      flex: 1;
      overflow: hidden;
    }

    &__title {
      line-height: .2rem;
      font-size: .14rem;
      font-weight: bold;
      color: $content-fontColor;
      @include ellipsis;
    }

    &__sales {
      margin: .06rem 0;
      line-height: .16rem;
      font-size: .12rem;
      color: $content-fontColor;
    }

    &__price {
      line-height: .2rem;
      font-size: .14rem;
      color: $highlight-fontColor;
    }

    &__yen {
      font-size: .1rem;
    }

    &__now {
      font-weight: bold;
    }

    &__origin {
      font-size: .1rem;
      color: $light-fontColor;
      text-decoration: line-through;
      margin-left: .06rem;
    }
  }

  &__number {
    position: absolute;
    right: 0;
    bottom: .12rem;
    height: .2rem;
    line-height: .2rem;

    .iconfont {
      font-size: .25rem;
    }

    &__minus {
      color: $medium-fontColor;
    }

    &__plus {
      color: $btn-bgColor;
    }

    &__count {
      font-size: .14rem;
      color: $content-fontColor;
      margin: 0 .1rem;
      vertical-align: top;
    }
  }
}
</style>
